(../public/fonts/CircularStd-Light.otf);\r\n  font-display: swap;\r\n}\r\n\r\n:root{\r\n  --bg: #202020;\r\n  --bg-darker: #181818;\r\n  --color: #EEE6F3;\r\n  --accent-col: #facd00;\r\n  --font-1: \"Circular-Black\", sans-serif;\r\n  --font-2: \"Circular-Medium\", sans-serif;\r\n  --font-3: \"Circular-Book\", sans-serif;\r\n  --font-4: \"Circular-Light\", sans-serif;\r\n}\r\n\r\n::-webkit-scrollbar{\r\n  display: none;\r\n}\r\n\r\n*{\r\n  padding: 0;\r\n  margin: 0;\r\n  box-sizing: border-box;\r\n}\r\nhtml{\r\n  scroll-behavior: smooth;\r\n}\r\n.bodyIndex{\r\n  background-color: var(--bg-darker);\r\n  color: var(--color);\r\n}\r\nheader{\r\n  display: flex;\r\n  position: fixed;\r\n  width: 100vw;\r\n  padding-block: 2vw;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  transition: .2s;\r\n  z-index: 1;\r\n}\r\n.skip{\r\n  position: fixed;\r\n  display: flex;\r\n  justify-content: center;\r\n  text-decoration: none;\r\n  font-family: var(--font-4);\r\n  font-size: 2rem;\r\n  background-color: transparent;\r\n  color: transparent;\r\n  top: 150px;\r\n}\r\n.skip:focus{\r\n  color: var(--bg-darker);\r\n  background-color: var(--accent-col);\r\n}\r\n.skip:not(:focus){ pointer-events: none; }\r\nheader h1{\r\n  display: flex;\r\n  flex-direction: column;\r\n  color: var(--color);\r\n  font-family: var(--font-4);\r\n  margin-left: clamp(15px, 1vw, 100px);\r\n  font-size: clamp(8px, 1.3vw, 100px) ;\r\n  padding: .5vw;\r\n  background-color: var(--bg);\r\n  transition: .2s;\r\n}\r\n.hamburger{\r\n  display: none;\r\n}\r\nheader nav{\r\n  display: flex;\r\n  margin-right: 4vw;\r\n  gap: 1vw;\r\n}\r\nheader nav a{\r\n  text-decoration: none;\r\n  color: var(--bg);\r\n  font-size: clamp(13px, 1.1vw, 100px);\r\n  font-family: var(--font-3);\r\n  transition: .2s;\r\n  text-align: center;\r\n  padding-block: clamp(20px, 1.8vw, 100px);\r\n  padding-inline: 1vw;\r\n  letter-spacing: 1px;\r\n}\r\nheader nav a:hover{\r\n  animation: blink 1s infinite;\r\n}\r\n@ke